s estos estudios determinan que la inclusión de una nueva sustancia en el mercado sea lenta o rápida, siendo esta una forma de garantizar la inocuidad para los seres humanos. Normalmente, estos estudios son llevados a cabo por organismos internacionales como el comité mixto de la FAO y OMS o la OCDE.")]),_c('Separador'),_vm._m(12),_vm._m(13),_vm._m(14),_vm._m(15),_c('h3',{staticClass:"bgtitulo2 py-1 px-3"},[_vm._v("Análisis toxicológico")]),_c('p',[_vm._v("Este análisis comprende tres etapas:")]),_vm._m(16),_c('p',[_vm._v("Con base en los estudios experimentales se obtienen los índices de toxicidad (IT) tales como el NOEL, la IDA y el LMR que constituyen valores de vital interés en la toxicología alimentaria.")]),_c('Separador'),_vm._m(17),_c('p',{staticClass:"mb-4"},[_vm._v("La toxicidad es un fenómeno muy complejo en el que actúan muchos factores y donde se pueden distinguir dos grandes fases: la fase toxicocinética y la fase toxicodinámica.")]),_vm._m(18),_c('p',{staticClass:"mb-4"},[_vm._v("En esta fase se da el movimiento del tóxico en el organismo y comprende los procesos de absorción, distribución, biotransformación y eliminación.")]),_c('div',{staticClass:"tarjeta bgc-slider p-4 p-lg-5 mb-5",attrs:{"data-aos":"fade-down"}},[_c('SlyderA',{staticClass:"tarjeta tarjeta--blanca p-4",attrs:{"tipo":"b"}},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-8 mb-4 mb-md-0"},[_c('h4',[_vm._v("Absorción")]),_c('p',[_vm._v("Cuando ingresa el tóxico en el organismo este se ve obligado a atravesar las membranas biológicas que son básicamente hidrófobas y muy liposolubles, lo que hace que sean atravesadas por sustancias lipídicas o grasas y no por sustancias hidrosolubles o ionizadas. ")]),_c('p',[_vm._v("Las sustancias atraviesan las membranas celulares por dos procesos fundamentales: Difusión (movimientos libres al azar producidos por moción cinética) y transporte activo (movimiento de las sustancias combinadas químicamente en la membrana con productos transportadores). La mayoría de las sustancias extrañas liposolubles se trasladan por difusión simple a través de las membranas hacia la bicapa lipídica. ")]),_c('p',[_vm._v("Otro mecanismo de transferencia del tóxico es atravesando la membrana mediante la filtración a través de los poros y la pinocitosis. Muchos iones disueltos en agua y ciertas sustancias hidrosolubles de pequeño peso molecular se expanden a través de los poros acuosos de la membrana celular. La velocidad de difusión de una sustancia a través de una membrana viene determinada por el tamaño o peso molecular, a mayor peso molecular, mayor dificultad de penetrar. Por su forma, cuando el tóxico está dividido en partículas muy pequeñas, tiene mayor facilidad de absorción; por su grado de ionización, donde las formas no ionizadas son las más liposolubles. Finalmente, por su liposolubilidad, que es una característica propia de cada sustancia y entre más liposoluble, más fácilmente penetrará. ")])]),_c('div',{staticClass:"col-lg-3"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/t1/slyder1.png"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-8 mb-4 mb-md-0"},[_c('h4',[_vm._v("Distribución")]),_c('p',[_vm._v("El tóxico se traslada principalmente por la sangre y posteriormente a los diferentes tejidos donde ejercerá su acción o bien se acumulará. En las sustancias transportadas por los alimentos, el tracto digestivo es el lugar principal de absorción y el de las sustancias muy liposolubles es habitualmente la boca. En el intestino delgado es donde se adsorben la mayor parte de los nutrientes y de otras sustancias, donde la carga iónica de las sustancias juega un papel importante en su velocidad de absorción. Debido a que el pH del intestino delgado está próximo a la neutralidad, los ácidos y las bases fuertes presentan carga, por lo tanto, se absorben menos que las sustancias neutras. La absorción del intestino delgado comparada con la del grueso, es relativamente pobre; sin embargo, el intestino delgado sirve de sitio de absorción de algunas sustancias, especialmente las producidas por la fermentación bacteriana en este órgano o las que son muy liposolubles. ")])]),_c('div',{staticClass:"col-lg-3"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/t1/slyder2.png"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-8 mb-4 mb-md-0"},[_c('h4',[_vm._v("Biotransformación")]),_c('p',[_vm._v("Son las biotransformaciones que tiene un tóxico en el organismo, por lo cual su finalidad es la formación de un compuesto hidrosoluble, poco tóxico y fácilmente eliminable por algún órgano de excreción. Esto a veces no se consigue y lo que ocurre es un proceso de bioactivación o biotoxificación, en donde se dan reacciones metabólicas de oxidación, principalmente de reducción e hidrólisis, por lo que aumenta la toxicidad de los tóxicos.")]),_c('p',[_vm._v("El movimiento interorgánico de sustancias en propiedades de diversas membranas y por las uniones con los líquidos. Además, el grado de perfusión sanguínea tiene un importante papel en el desempeño de las sustancias.")]),_c('p',[_vm._v("El flujo sanguíneo total es mucho mayor en el hígado, riñón, músculo, cerebro y piel que en la grasa y en el hueso. Por tanto, los órganos con mayor flujo sanguíneo, es de esperar que contengan la mayor cantidad de sustancias en movimiento.")]),_c('p',[_vm._v("Las sustancias orgánicas más polares tienden a unirse a las proteínas sanguíneas y a los tejidos blandos, mientras que las orgánicas cuyas propiedades químicas son parecidas a las del calcio, suelen almacenarse en tejido óseo. Los tejidos sirven como acumuladores en la absorción de los materiales más liposolubles. El colágeno, la principal proteína del organismo, liga muchos iones, como calcio, bario, magnesio, estroncio, berilio, plomo, arsénico y mercurio. El hueso es el almacén de ciertos elementos inorgánicos, tales como el plomo y el estroncio. El hígado tiene gran afinidad para ligar ácidos orgánicos, colorantes y ciertos esteroides. ")])]),_c('div',{staticClass:"col-lg-3"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/t1/slyder3.png"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-8 mb-4 mb-md-0"},[_c('h4',[_vm._v("Eliminación")]),_c('p',[_vm._v("Se realiza a través de la excreción de las sustancias tóxicas o de los residuos de la biotransformación mediante la orina, bilis, heces, los líquidos orgánicos, está gobernada por la respiración (aire expirado) y otras como sudor, saliva, leche materna, entre otras.")]),_c('p',[_vm._v("La excreción urinaria es la más importante de las distintas rutas por las que pueden eliminarse del organismo las sustancias tóxicas consumidas en la dieta. Esto se aplica tanto al número, como a las cantidades de sustancias excretadas. La excreción fecal es importante para los compuestos que no se han absorbido en el tracto gastroentérico o que son secretados por la bilis. En las mujeres lactantes la excreción de sustancias con la leche desempeña cierto papel en la eliminación de productos tóxicos; algunos fármacos, ciertos plaguicidas y los agentes tóxicos de los alimentos enmohecidos se excretan en parte por la leche, sin embargo, hay que considerar en estos casos el impacto de las sustancias excretadas sobre el infante alimentado.")])]),_c('div',{staticClass:"col-lg-3"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/t1/slyder4.png"),"alt":""}})])])])],1),_vm._m(19),_vm._m(20),_c('Separador'),_vm._m(21),_c('p',{staticClass:"mb-4"},[_vm._v("Se pueden distinguir un grupo de sustancias extrañas al organismo vivo y que tras su interacción pueden originar un efecto nocivo, las cuales son los tóxicos endógenos o propios del alimento y los tóxicos exógenos o ajenos al alimento como se muestran en la Tabla 1.")]),_c('TabsC',{staticClass:"color-acento-contenido mb-5"},[_c('div',{staticClass:"bg4 p-4 py-md-5",attrs:{"titulo":"Tóxicos endógenos"}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-6 col-lg-5 mb-3 mb-md-0"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/t1/tab1.svg"),"alt":""}})]),_c('div',{staticClass:"col-md-6 col-lg-5"},[_c('p',[_vm._v("Son sustancias que se encuentran presentes de modo natural en los alimentos o se generan en la evolución natural de los mismos. En este grupo se encuentran los antinutrientes y las sustancias tóxicas de origen vegetal y animal.")])])])]),_c('div',{staticClass:"bg4 p-4 py-md-5",attrs:{"titulo":"Tóxicos exógenos"}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-6 col-lg-5 mb-3 mb-md-0"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/t1/tab2.svg"),"alt":""}})]),_c('div',{staticClass:"col-md-6 col-lg-5"},[_c('p',[_vm._v("Son sustancias extrañas a los alimentos que pueden ser de diversas clases: adicionadas al alimento, generadas por el procesamiento tecnológico-industrial de estos o procedentes de la contaminación ambiental. A este grupo pertenecen los aditivos, las sustancias tóxicas de origen bacteriano y fúngico, las que surgen de la contaminación ambiental, los residuos de medicamentos en alimentos y los tóxicos generados durante la preparación, proceso y almacenamiento de los alimentos.")])])])])]),_c('p',{staticClass:"mb-4"},[_vm._v("En tiempos pasados, los ancestros se alimentaban con una serie de productos que desencadenaban muchos casos de intoxicación y muertes por desconocimiento que estos alimentos causaban cierta enfermedad. Gracias a la experiencia, el hombre aprendió a distinguir cuáles sustancias podría utilizar como alimento, sin producirle enfermedades ni causarle la muerte, y cuáles no; también sobre cuáles debería procesar previamente antes de consumirlas. Así se desarrollaron hábitos alimenticios que permitieron la supervivencia y el desarrollo de la especie.")]),_vm._m(22),_vm._m(23),_vm._m(24),_c('Separador'),_vm._m(25),_c('div',{staticClass:"bloque-texto-g bloque-texto-g--inverso color-acento-contenido p-3 p-sm-4 p-md-5 mb-5",attrs:{"data-aos":"fade-down"}},[_c('div',{staticClass:"bloque-texto-g__img",style:({'background-image': ("url(" + (require('@/assets/curso/temas/t1/img13.png')) + ")")})}),_vm._m(26)]),_vm._m(27),_vm._m(28),_c('p',{staticClass:"mb-4"},[_vm._v("Las proteasas son enzimas que hidrolizan los enlaces peptídicos de las proteínas y péptidos, permitiendo así su asimilación. Los inhibidores de proteasa, se encuentran tanto en productos vegetales como en los de origen animal. Se localizan en numerosas variedades de leguminosas, en cacahuetes, patata, batata, y también en los huevos (ovomucoide y ocoinhibidor), en el calostro y la leche.")]),_vm._m(29),_vm._m(30),_vm._m(31),_c('div',{staticClass:"tarjeta bgc-slider p-4 p-lg-5 mb-5",attrs:{"data-aos":"fade-down"}},[_c('SlyderA',{staticClass:"tarjeta tarjeta--blanca p-4 pt-5",attrs:{"tipo":"b"}},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-11 mb-4 mb-md-0"},[_c('h4',[_vm._v("Antivitaminas")]),_c('p',[_vm._v("Son compuestos mayormente de naturaleza proteica que actúan sobre algunas vitaminas disminuyendo o destruyendo su función biológica, induciendo el aumento de las exigencias de las vitaminas. Entre las más importantes se encuentran la avidina, la tiaminasa, la niacitina y el ácido ascórbico oxidasa.")]),_c('img',{attrs:{"src":require("@/assets/curso/temas/t1/slid1.png"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-11 mb-4 mb-md-0"},[_c('h4',[_vm._v("Avidina")]),_c('p',[_vm._v("Se encuentra en la clara de huevo y es capaz de bloquear la absorción de la biotina (vitamina H o vitamina B8), por formación de un precipitado insoluble, afectando a su biodisponibilidad. La avidina es una glicoproteína que se combina con dos moléculas de biotina inhibiendo su absorción. Se inactiva por ebullición en agua en pocos minutos. El consumo excesivo de huevo de gallina crudo produce síntomas de falta de biotina con dermatitis exfoliativa y conjuntivitis.")]),_c('img',{attrs:{"src":require("@/assets/curso/temas/t1/slid2.png"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-11 mb-4 mb-md-0"},[_c('h4',[_vm._v("Tiaminasa")]),_c('p',[_vm._v("Está presente en animales como peces, moluscos, crustáceos y en vegetales como coles de bruselas, espinacas, remolacha, semillas de mostaza, fresas, café, té, entre otros. El efecto de la tiaminasa ha sido atribuido a la presencia de una serie de sustancias con estructura química variada, pero derivada de los hidroxifenoles. ")]),_c('p',[_vm._v("Entre estos compuestos hay que señalar el ácido clorogénico presente en los granos verdes de café o el metilsinapato en las semillas de mostaza. El mecanismo de inactivación de la tiamina (vitamina B1) por parte de estos compuestos requiere oxígeno y es dependiente de la temperatura y del pH, siendo una reacción de óxido-reducción. La tiaminasa es termolábil, por ello se inactiva por cocción. Esta sustancia es la causante de muchos casos de parálisis y perturbaciones neurológicas.")]),_c('img',{attrs:{"src":require("@/assets/curso/temas/t1/slid3.png"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-11 mb-4 mb-md-0"},[_c('h4',[_vm._v("Niacitina")]),_c('p',[_vm._v("Se trata de una sustancia que se encuentra en el maíz. Es un precursor del ácido nicotínico o niacina (vitamina B3). En este compuesto el ácido se encuentra fuertemente acomplejado unido a un polipétido. Este complejo es resistente a las enzimas digestivas, con lo que se impide la asimilación de la niacina.")]),_c('p',[_vm._v("Solo se libera tras la acción alcalina, de ahí que en países con dietas basadas casi exclusivamente en maíz, como el caso de México, el cual utiliza una gran cantidad de maíz para hacer las “tortillas” típicas, tengan que tratar este producto con cal durante un tiempo para que se libere la niacina. La deficiencia de niacina produce pelagra que está asociada a alteraciones del sistema nervioso, trastornos digestivos, fatiga y problemas de piel. Esta antivitamina es inactivada por el calor.")]),_c('img',{attrs:{"src":require("@/assets/curso/temas/t1/slid4.png"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-7"},[_c('h4',[_vm._v("Ácido ascórbico oxidasa")]),_c('p',[_vm._v("Esta enzima oxida el ácido ascórbico (vitamina c) por la presencia de oxígeno que es el que cataliza la oxidación. Se encuentra presente en muchos alimentos de origen vegetal, siendo activa en pH entre 4-6 y temperatura entre 15-30 °C. Su actividad se inhibe cuando el alimento es sometido a temperaturas más altas, por ejemplo, cuando se escaldan los vegetales. Se encuentra en el pepino, calabaza, melón, col, tomate, lechuga, coliflor, espinaca, zanahoria, patata, guisantes, entre otras. También se puede evitar la acción de la antivitamina por el consumo rápido (tiempo transcurrido desde su preparación o después de abrir el envase) de los zumos de fruta. ")])]),_c('div',{staticClass:"col-lg-4"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/t1/slid5.png"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-7"},[_c('h4',[_vm._v("Sustancias que impiden la asimilación y metabolismo de minerales")]),_c('p',[_vm._v("Estos componentes no causan ninguna toxicidad en circunstancias de consumo normal, pero sí pueden interferir en la absorción de minerales y metabólicamente esto puede ser de gravedad si se presentan problemas de malnutrición. Entre las sustancias más importantes dentro de este género se encuentran el ácido oxálico y el ácido fático.")])]),_c('div',{staticClass:"col-lg-4"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/t1/slid6.png"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-6"},[_c('h4',[_vm._v("Acido oxálico")]),_c('p',[_vm._v("Los oxalatos pueden fijar minerales como el hierro, magnesio, calcio y zinc, pero cuando forman sales con sodio y potasio e insolubles con metales divalentes como el calcio interfieren en su asimilación. Se encuentran en algunos alimentos de origen vegetal como en las espinacas, la remolacha, el cacao, las patatas, el té, el café, el ruibarbo y las acelgas.")]),_c('p',[_vm._v("Sin duda el principal problema nutricional del oxalato deriva de su efecto negativo sobre la asimilación del calcio, principalmente en la fase en la cual el aporte de calcio es primordial como en el crecimiento y gestación, por lo cual se recomienda el consumo de alimentos ricos en calcio, como los productos lácteos, así como un aumento en la ingesta de vitamina D que ayuda a su asimilación.")]),_c('p',[_vm._v("La intoxicación por ácido oxálico causa dolor abdominal y gastroenteritis y en algunos casos graves puede causar diarrea, vómitos, convulsiones, alteraciones en la coagulación y en estados finales se puede llegar al coma. En el aparato digestivo de los seres humanos hay bacterias que pueden digerir el ácido oxálico, lo que facilita cierta adaptación a productos ricos en este compuesto.")])]),_c('div',{staticClass:"col-lg-5"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/t1/slid7.png"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-6"},[_c('h4',[_vm._v("Ácido fítico")]),_c('p',[_vm._v("Este ácido impide la absorción de calcio, magnesio, hierro, cobre y zinc, además de afectar la utilización proteica por la formación de complejos insolubles. Se encuentra especialmente en cereales, leguminosas y semillas oleaginosas, trigo, maíz, arroz, avena, lenteja, soja, cacahuete y nuez. En los cereales se halla en la parte exterior, que es donde está su gran contenido en fitatos, por ejemplo, en productos como el salvado, harinas, pan y pastas. ")]),_c('p',[_vm._v("Se han registrado casos de retrasos en el crecimiento y maduración sexual en niños cuya dieta se basa fundamentalmente en la ingesta de pan integral total. No obstante, la reducción en la biodisponibilidad mineral se puede contrarrestar en cierta medida, incrementando la ingesta de minerales esenciales y gracias al aporte suficiente de mineral existente en la mayor parte de semillas.")]),_c('p',[_vm._v("En muchos alimentos, los niveles de fitatos pueden ser reducidos por la enzima fosfatasa, que se encuentra presente en los alimentos ricos en ácido fítico con mayor o menor actividad. Gracias a la actividad de esta enzima, el ácido se hidroliza liberándose inositol y fosfato orgánico. Esta enzima presenta una actividad máxima a pH 5,5 y 60 °C, con lo cual el tratamiento del alimento con ácido y/o calor favorece su hidrólisis, y gracias a ello el contenido de este ácido se reduce drásticamente durante el proceso de cocción. Las fitasas son inhibidas por cocción a más de 60 °C,por fermentación del pan con levadura química (bicarbonato) que provoca aumento de pH y por la germinación.")])]),_c('div',{staticClass:"col-lg-5"},[_c('div',{staticClass:"tarjeta bg10 p-4 py-lg-5 mb-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("Por otra parte, el ácido fítico tiene algunos efectos beneficiosos para el organismo, tales como:")])]),_c('div',{staticClass:"tarjeta bg7 p-4 py-lg-5 mb-4"},[_c('ul',{staticClass:"lista-ul--color fa-ul color-secundario mb-0"},[_c('li',{staticClass:"mb-0"},[_c('i',{staticClass:"fas fa-check-circle"}),_c('span',{staticClass:"mb-0"},[_c('b',[_vm._v("Efecto antioxidante:")]),_vm._v(" inhibe la oxidación de lípidos y la acumulación de peróxidos en los alimentos.")])])])]),_c('div',{staticClass:"tarjeta bg7 p-4 py-lg-5 mb-4"},[_c('ul',{staticClass:"lista-ul--color fa-ul color-secundario mb-0"},[_c(