exto p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("Los datos de dosis - respuesta y en particular, la información sobre la toxicidad aguda de las sustancias, se indica a menudo como respuesta acumulativa frente a las dosis. En este caso se suministran a grupos de individuos varias cantidades de la sustancia y se anota el porcentaje de los mismos que responden positivamente. En el caso de la dosis letal, se apunta el número de seres que mueren. En el caso de una respuesta no terminal, se escribe el número de los individuos que responden al menos a una de las dosis. Previamente, se establece experimentalmente el intervalo de porción en el que tiene lugar la respuesta de interés. Los datos se representan como porcentaje acumulativo de individuos que responden en la forma deseada frente a la dosis.")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-4",attrs:{"data-aos":"fade-down"}},[_c('div',{staticClass:"col-12"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido"},[_c('h5',[_vm._v("Figura 1. ")]),_c('span',[_vm._v("Curva dosis - respuesta")])])]),_c('div',{staticClass:"col-lg-5"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/t1/fig1.svg"),"alt":"La grafica presenta la curva en forma de montaña que se forma al indicar las respuestas de dosis acumulativas de toxicidad de un elemento o compuesto."}}),_c('figcaption',[_c('b',[_vm._v("Fuente:")]),_vm._v(" Shibatomo")])]),_c('div',{staticClass:"col-lg-7"},[_c('p',{staticClass:"mb-4"},[_vm._v("A medida que la dosis de una sustancia se aproxima al nivel tóxico, se llega a un intervalo de dosis al que los individuos del grupo experimental responden de forma similar. Una vez que se ha establecido la respuesta, los experimentos de dosis - respuesta o dosis - efecto pueden mostrarse de distintas formas. Se obtiene una dosis de frecuencias de respuestas (Figura 1) representando el número de individuos con una respuesta definida específica en función de la dosis. La mayor de estas dosis sin respuesta es la llamada dosis o nivel sin efecto observado, en inglés "),_c('em',[_vm._v("“No Observed Effect Level”")]),_vm._v(" (NOEL).")]),_c('div',{staticClass:"cajon color-primario bg1 p-4 p-lg-5 mb-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("Para establecer la NOEL se administra un rango de dosis suficientemente pequeña para originar respuesta. A medida que se acumula la dosis, el porcentaje de individuos de cada grupo experimental que responde sigue aumentando hasta que se alcanza una dosis, a partir de la cual responde el 100 % de los miembros del grupo.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bg2 p-4 mb-4"},[_c('div',{staticClass:"row align-items-center"},[_c('div',{staticClass:"col-lg-2"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/t1/icono2.svg"),"alt":""}})]),_c('div',{staticClass:"col-lg-10"},[_c('p',{staticClass:"mb-0"},[_vm._v("La seguridad absoluta de una sustancia no puede explicarse, puesto que la demostración ha de basarse en una evidencia negativa, es decir, en la falta de peligro o de daño debido a tal sustancia. Estadísticamente, siempre existe la posibilidad que el experimento siguiente demuestre que el producto no es seguro. Así, el concepto de seguridad se ha desarrollado con el transcurso del tiempo e inicialmente se podía considerar segura una sustancia si se consumía sin que ocasionase la muerte inmediata o una lesión aguda. Los conocimientos actuales de los efectos tóxicos y la capacidad de comprobarlos han aumentado hasta un punto, en el que se considera relativamente segura a una sustancia si no se producen efectos adversos en determinados efectos biológicos.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-lg-3"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/t1/img5.svg"),"alt":""}})])},function () {var _vm=this;var _h=_vm.$creat